.slider {
  position: relative;
  width: 280px;
  height: 256px;

  @media (min-width: $mobile-above) {
    width: 560px;
    height: 512px;
  }
}

.slider__before-wrapper,
.slider__after-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.slider__before-wrapper {
  z-index: 2;
}

.slider__after-wrapper {
  z-index: 1;
}

.slider__before-image-mobile,
.slider__after-image-mobile {
  display: inline;
  width: 280px;
  height: 256px;

  @media (min-width: $mobile-above) {
    display: none;
  }
}

.slider__before-image-tablet,
.slider__after-image-tablet {
  display: none;
  width: 560px;
  height: 512px;

  @media (min-width: $mobile-above) {
    display: inline;
  }
}

.slider__button-wrapper {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  width: 40px;
}

.slider__button {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  width: 40px;
  height: 40px;
  background-color: transparent;
  background-image: url("../img/svg/example-slider-control-button.svg");
  border: none;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}

.slider__button-line {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  z-index: 1;
  width: 4px;
  background-color: $svg-icons-color;
}

.slider__button:hover,
.slider__button:focus {
  opacity: 0.8;
}

.slider__button:hover + .slider__button-line,
.slider__button:focus + .slider__button-line {
  opacity: 0.8;
}
